@import url('https://fonts.googleapis.com/css2?family=Dosis:wght@800&family=Josefin+Sans:wght@100&family=Nova+Round&display=swap');

$font-1: roboto,Helvetica,sans-serif;
$font-2: montserrat,Helvetica,sans-serif;
$font-3: 'Josefin Sans', serif;

$color-1: #161116;
$color-2: #49515d;
$color-3: #7083d6;
$color-4: #a33588;
$color-5:  #49515d;;
$white: rgb(255, 251, 251);
$black: #333;
$grey: #9E9E9E;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$minHeight: 20vh;

@mixin verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center; 
  }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 0.85rem;
  @media screen and (min-width: 1600px){
    font-size: 1.1rem;
  }
}

body {
    margin: 1rem auto;
    font-family: $font-1;
    color: $black;
    background: url('../media/main_bg.png') center/cover;
    background-color: #f5f5f5;
    -webkit-background-size: cover;
    background-attachment: fixed;
    background-position: center center;
    background-repeat: no-repeat;
  }

  button {
    cursor: pointer;
  }
  li {
    list-style-type: none;
  }
  a {
    text-decoration: none;
    color: $black;
    cursor: pointer;
  }
  a:visited {
    color:$black;
  }

  .home,.knowledges, .portfolio, .contact {
  
    @media screen and (max-width: 850px){
      display: block !important;
    }
  }
  .homeContent, .knowledgesContent, .portfolioContent, .contactContent {
    margin-left: .4rem;
  
    @media screen and (max-width: 850px){
      margin-left: 0 !important;
    }
  }