.knowledges {
  max-width: 60%;
  margin: auto;
 
  .knowledgesContent {
      min-height: $minHeight;
      width: 100%;
      border-radius: $borderRadius;
      position: relative;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      margin: auto;
      box-shadow: 12px 12px 4px 1px rgba(0, 0, 0, .2);

    .header{
        background-color: #2eca7f;
        background-image: url('../../media/sp_main_bg.png');
        color: $white;
        flex:1;
        min-height: 10%;
        display: flex;
        h1{
          margin: auto;
        }
    }

    .grille {
      min-height: $minHeight;
      width: 100%;
      position: relative;
      overflow: hidden;
      display: grid;
      grid-template-columns: 20% 1fr 1fr;
      grid-template-rows: 100%;
      grid-template-areas:
        "L E E";

      .languagesFrameworks,
      .experience,
      .otherSkills,
      .hobbies {
        background: $white;
        padding: 0.8rem;
        margin: 0;
      }
      h3 {
        font-size: 1rem;
        margin-bottom: 1rem;
        font-family: $font-2;
        color: $color-5;
        
      }

      .languagesFrameworks {
        grid-area: L;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
        grid-template-areas:
          "L"
          "F";

        .languageDisplay {
          grid-area: L;
        }
        .frameworkDisplay {
          grid-area: F;
        }
        h3 {
          margin-bottom: 0.3rem;
        }

        .years {
          font-size: 0.65rem;
          display: flex;
          justify-content: space-between;
          color: $color-5;

          span {
            width: 36%;

            &:nth-child(2) {
              text-align: center;
              width: 20%;
            }
            &:nth-child(3) {
              text-align: right;
              width: 44%;
            }

            &:nth-child(2):before,
            &:nth-child(3):before {
              content: "";
              background: rgba(180, 179, 179, 0.507);
              position: absolute;
              height: 146px;
              width: 1px;
              transform: translate(9px, 19px);
            }
          }
        }
        .languagesList {
          padding-top: 0.5rem;

          li {
            color: $color-1;
          }
          .progressBar {
            height: 0.4rem;
            background: transparent;
            border-radius: 50px;
            margin-top: 0.2rem;
            position: relative;

            &:after {
              content: "";
              position: absolute;
              background: #2eca7f;
              border-radius: 50px;
              height: 0.4rem;
              width: 0;
              animation: line 2s ease-out forwards;

              @keyframes line {
                to {
                  width: 100%;
                }
              }
            }
          }
        }
      }
      .experience {
        grid-area: E;
        font-size: 0.7rem;

        h3 {
          grid-area: 1;
        }
        h4 {
          font-size: 0.9rem;
          color: $color-5;
        }
        h5 {
          color: $color-3;
          padding: 0.5rem 0 0.5rem 1rem;
        }
        p {
          text-align: justify;
          padding: 0 1rem;
        }

       
        li {
            list-style-type: circle;
            margin: 10px;
      
        }
        
        .exp {
         
          margin: 20px;
          h4{
            background-color: #2eca7f;
            color: $white;
          }

          p{
            margin: 10px;
          }
          
        }

        .missionSumup{
           td { padding-left: 8px; }
           .gras {
            font-weight: bold;
           }
        }

        .showInfos {
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          background: rgba(0, 0, 0, 0.507);
          animation: popup 0.3s ease forwards;
          transform-origin: center;
  
          .infosContent {
            position: relative;
            width: 90%;
            margin: 0 auto;
            //color: $color-5;
            background: $white;
            border-radius: $borderRadius;
            padding: 2rem;
            box-shadow: 0 10px 10px #333;
            animation: popup 0.6s ease forwards;
            transform: scale(0);
            transform-origin: center;
  
            @keyframes popup {
              to {
                transform: scale(1);
              }
            }
          }
          .head {
            display: flex;
            justify-content: space-around;
            margin: 1rem 0;
          }
          h2 {
            text-align: left;
            margin-bottom: 1rem;
          }
          .button {
            cursor: pointer;
            color: $white;
            background: #2eca7f;
            border-radius: $borderRadius;
            padding: 1rem;
            transition: 0.2s;
  
            &:hover {
              background: $color-3;
            }
          }
          .text {
            text-align: justify;
            padding-bottom: 1rem;
          }
          .return {
            margin: 1rem auto;
            width: 200px;
          }
        }


        
      }
    }
  }
}
@media screen and (max-width: 950px) {
  .knowledgesContent {
    h3 {
      font-size: 1.2rem !important;
    }
    .languagesList {
      padding-top: 0.2rem !important;
    }
    p {
      font-size: 0.8rem;
    }
    .otherSkills,
    .hobbies {
      font-size: 0.8rem;
    }
  }
}
@media screen and (max-width: 850px) {
  .knowledgesContent {
    h4 {
      font-size: 0.9rem !important;
    }
    h5 {
      font-size: 0.6rem !important;
      padding: 0.2rem 1rem !important;
    }
  }
}
@media screen and (max-width: 500px) {
  .knowledgesContent {
    display: grid !important;
    grid-template-columns: 1fr !important;
    grid-template-rows: 1fr 1fr 180px 180px !important;

    .languages {
      grid-area: 1 !important;
    }
    .experience {
      grid-area: 2 !important;
      margin-left: 0 !important;
    }
    .otherSkills {
      grid-area: 3 !important;
      margin-right: 0 !important;
    }
    .hobbies {
      grid-area: 4 !important;
      margin-top: 0.5rem !important;
    }
  }
}
