.home {
  max-width: 60%;
  margin: auto;
    .homeContent {
      background: linear-gradient(115deg, $color-1 50%, transparent 100%);
      min-height: $minHeight;
      width: 100%;
      min-height: 70vh;
      border-radius: $borderRadius;
      position: relative;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      box-shadow: 12px 12px 4px 1px rgba(0, 0, 0, .2);
      margin: auto;
  
      

      .cardTop{

        display:flex;
        flex:1;
        background-color: #2eca7f;
        background-image: url('../../media/sp_main_bg.png');
        color: $white;

        .mypicture{
          flex:1;
          display:flex;
          justify-content: center;
          margin: auto;
          img{
            border: white 2px solid;
            width: 200px;
            height: 200px;
            box-shadow: 12px 12px 4px 1px rgba(0, 0, 0, .2);
          }
        }

        .id{
          margin: auto;
          flex:1;
          display:flex;
          flex-direction: column;
          justify-content: space-between;
          text-align: center;

          h1,h2{
            margin:10px;
          }

          .socialNetwork {
            margin:10px;
            ul {
              display: flex;
              padding: 10 14px;
              justify-content: center;
        
              li {
                margin: 10px;
                font-size: 1.5rem;
                background: $white;
                color: $grey;
                border-radius: 50%;
                padding: .5rem 0.8rem;
                transition: .3s;
                cursor: pointer;
        
                a:visited {
                  color: $color-1;
                }
                &:hover {
                  background: $color-3;
                }
              }
            }
          }


        }
      }

      .cardBottom{

        display:flex;
        flex:1;
        background-color: $white;

        *{
          margin: 10px;
        }

        .about{
          flex:1;
          line-height: 1.5em;
          margin-left: 40px;

          h1{
            margin-bottom: 60px;
          }
        
        }

        .address{
          flex:1;
          display: flex;
          justify-content: center;
          margin:auto;

          ul{
            
          }
          li{
            margin:10px;
          }

          ul.val li{
            color: $grey;
          }

        }

      }
    }
  }
  @media screen and (max-width: 850px){
    .homeContent {
      margin: 0 !important;
    }
  }
  @media screen and (max-width:580px){
    .homeContent {
      &:after {
      background: url('../../media/main_bg.png') no-repeat 60% 50%/cover !important;
      }
      h1 {
        font-size: 3rem !important;
      }
      h2 {
        font-size: 1.8rem !important;
      }
    }
  }