.sidebar {
    font-family: $font-1;
    display: flex;
  
    .id {
      padding: 1rem 1rem;
      
      .idContent {
        img {
          border-radius: 50%;
          box-shadow: 0 0 0 2px $color-4;
        }
        
          font-family: $font-2;
          font-size: 14px;
          color: $color-5;
      }
    }
    .navigation {
      padding: 1rem 1rem;
      flex: 1;
      ul {
        height: 100%;
        display: flex;
        justify-content: flex-end;
        li {
          font-size: 10px;
  
          &:hover i {
            transform: translate(3px, 3px);
          }
  
          a {
          color: $color-5;
          }
          .navActive {
            color: $color-4;
          }
  
          i {
            font-size: 0.95rem;
            transform: translateY(3px);
            transition: .2s ease;
          }
          span {
            margin-left: 2.7rem;
          }
        }
      }
    }
    
  }
  @media screen and (max-width: 850px){
    .sidebar {
      display: flex;
      min-height: 50px;
      margin-bottom: .2rem;
  
      .id, .socialNetwork {
        display: none;
      }
      .navigation {
        width: 100%;
        padding: .1rem 1rem;
  
        ul {
          display: flex;
          justify-content: space-between;
  
          span {
            margin-left: 1.5rem !important;
          }
        }
      }
    }
  }
  @media screen and (max-width:580px){
    .sidebar {
      ul {
        justify-content: space-around !important;
  
        li {
          padding: 1rem 0 !important;
        }
        i {
          font-size: 1.4rem !important;
          transform: translateY(-3px) !important;
        }
        span {
          display: none;
        }
      }
    }
  }